/* Add or modify the following styles to your existing button CSS */

.btn {
    padding: 10px 20px;
    /* Increase padding for a rectangular shape */
    border-radius: 5px;
    /* Add border-radius for rounded corners */
    outline: none;
    border: none;
    cursor: pointer;
    font-weight: bold;
    /* Make the text bolder */
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 10px 20px;
    /* Increase padding for a rectangular shape */
    font-size: 18px;
}

.btn--large {
    padding: 14px 30px;
    /* Increase padding for a larger rectangular shape */
    font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}