/* Footer.css */

.footer-container {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    padding: 2rem 0 2rem 0;
    /* Adjusted top padding */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Social Icons */
.social-icon-link {
    color: #fff;
    font-size: 24px;
    /* Adjusted font size */
}

.social-media {
    max-width: 1000px;
    width: 100%;
}

.social-media-wrap {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
    max-width: 1000px;
    margin: 20px auto 0 auto;
    /* Adjusted margin */
}

.social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 260px;
    /* Adjusted width */
    gap: 100px;
}

.social-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

@media screen and (max-width: 820px) {
    .social-media-wrap {
        flex-direction: column;
    }
}

